.TodoItem {
  background-color: #fafafa;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 24px auto;
  border-radius: 10px;
  width: 70%;
  max-width: 450px;
  min-width: 200px;
  /* box-shadow: 0px 5px 10px rgba(190, 192, 194, 0.952); */
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  /* color: blue; */
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
  position: absolute;
  left: 10px;
  top: 10px;
  font-size: 30px;
}
.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  top: 10px;
  right: 0;
  font-size: 40px;
}

.Icon-delete:hover {
  color: #df2e2e;
}
